"use client";

import { useEffect, useState } from "react";
import { useParams, useRouter } from "next/navigation";

const CharactersPage = () => {
  const params = useParams();
  const router = useRouter();
  const { id } = params;

  const [characters, setCharacters] = useState<any[]>([]);
  const [filteredCharacters, setFilteredCharacters] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [roleFilter, setRoleFilter] = useState("all");
  const [sortBy, setSortBy] = useState("name");
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 20;

  const pageBackgroundStyle = {
    position: "fixed" as const,
    top: 0,
    left: 0,
    width: "100vw",
    height: "100vh",
    background: "linear-gradient(135deg, #0f0c29 0%, #302b63 50%, #24243e 100%)",
    backgroundAttachment: "fixed",
    zIndex: -1
  };

  const contentStyle = {
    position: "relative" as const,
    zIndex: 1,
    padding: "20px",
    paddingTop: "80px",
    minHeight: "100vh"
  };

  useEffect(() => {
    async function loadCharacters() {
      if (!id) return;
      setLoading(true);
      try {
        const res = await fetch(`https://api.jikan.moe/v4/anime/${id}/characters`);
        const json = await res.json();
        const data = json.data || [];
        setCharacters(data);
        setFilteredCharacters(data);
      } catch (error) {
        console.error("Erro:", error);
      } finally {
        setLoading(false);
      }
    }
    loadCharacters();
  }, [id]);

  useEffect(() => {
    let results = characters;
    
    // Filtro por busca
    if (searchTerm.trim() !== "") {
      const term = searchTerm.toLowerCase();
      results = results.filter(character => 
        character.character.name.toLowerCase().includes(term) ||
        character.role.toLowerCase().includes(term) ||
        character.voice_actors?.some((va: any) => 
          va.person.name.toLowerCase().includes(term)
        )
      );
    }
    
    // Filtro por papel
    if (roleFilter !== "all") {
      results = results.filter(character => 
        character.role.toLowerCase() === roleFilter.toLowerCase()
      );
    }
    
    // Ordena√ß√£o
    results.sort((a, b) => {
      if (sortBy === "name") {
        return a.character.name.localeCompare(b.character.name);
      } else if (sortBy === "role") {
        return a.role.localeCompare(b.role);
      }
      return 0;
    });
    
    setFilteredCharacters(results);
    setCurrentPage(1); // Reset para primeira p√°gina ao filtrar
  }, [searchTerm, roleFilter, sortBy, characters]);

  // Pagina√ß√£o
  const totalPages = Math.ceil(filteredCharacters.length / itemsPerPage);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const paginatedCharacters = filteredCharacters.slice(startIndex, endIndex);

  const uniqueRoles = Array.from(
    new Set(characters.map(c => c.role.toLowerCase()))
  ).sort();

  const handlePageChange = (page: number) => {
    setCurrentPage(page);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  return (
    <>
      <div style={pageBackgroundStyle} />
      
      <div style={contentStyle}>
        <div style={{ maxWidth: "1200px", margin: "0 auto" }}>
          {/* Cabe√ßalho */}
          <div style={{ 
            display: "flex", 
            justifyContent: "space-between", 
            alignItems: "center", 
            marginBottom: 30,
            flexWrap: "wrap",
            gap: 15
          }}>
            <h1 style={{ 
              color: "white", 
              margin: 0, 
              fontSize: "2.2rem",
              textShadow: "0 2px 4px rgba(0,0,0,0.5)",
              background: "rgba(0, 0, 0, 0.6)",
              padding: "15px 25px",
              borderRadius: "12px",
              border: "2px solid rgba(255, 255, 255, 0.1)"
            }}>
               Personagens
            </h1>
            <button 
              onClick={() => router.push(`/${id}`)}
              style={{ 
                padding: "12px 25px", 
                background: "linear-gradient(135deg, #1e88e5, #0d47a1)", 
                color: "white", 
                border: "none", 
                borderRadius: "10px", 
                cursor: "pointer",
                fontWeight: "bold",
                fontSize: "16px",
                transition: "all 0.3s",
                display: "flex",
                alignItems: "center",
                gap: "10px",
                boxShadow: "0 4px 15px rgba(30, 136, 229, 0.4)"
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.transform = "translateY(-3px)";
                e.currentTarget.style.boxShadow = "0 8px 20px rgba(30, 136, 229, 0.6)";
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.transform = "translateY(0)";
                e.currentTarget.style.boxShadow = "0 4px 15px rgba(30, 136, 229, 0.4)";
              }}
            >
              <span>‚Üê</span>
              <span>Voltar ao Anime</span>
            </button>
          </div>

          {/* Filtros */}
          <div style={{ 
            background: "rgba(0, 0, 0, 0.8)", 
            padding: "25px", 
            borderRadius: "15px",
            marginBottom: "30px",
            backdropFilter: "blur(10px)",
            border: "2px solid rgba(255, 255, 255, 0.15)",
            boxShadow: "0 10px 30px rgba(0, 0, 0, 0.4)"
          }}>
            <div style={{ 
              display: "flex", 
              flexDirection: "column",
              gap: "20px"
            }}>
              {/* Barra de busca */}
              <div style={{ display: "flex", gap: "15px", flexWrap: "wrap" }}>
                <input
                  type="text"
                  placeholder="üîç Buscar por nome, papel ou dublador..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  style={{
                    padding: "14px 20px",
                    borderRadius: "10px",
                    border: "2px solid rgba(255, 255, 255, 0.2)",
                    background: "rgba(0, 0, 0, 0.9)",
                    color: "white",
                    flex: "1",
                    minWidth: "250px",
                    outline: "none",
                    fontSize: "16px",
                    transition: "all 0.3s"
                  }}
                  onFocus={(e) => e.target.style.borderColor = "#1e88e5"}
                  onBlur={(e) => e.target.style.borderColor = "rgba(255, 255, 255, 0.2)"}
                />
                
                {/* Select de Papel */}
                <select
                  value={roleFilter}
                  onChange={(e) => setRoleFilter(e.target.value)}
                  style={{
                    padding: "14px 45px 14px 20px",
                    borderRadius: "10px",
                    border: "2px solid rgba(255, 255, 255, 0.3)",
                    background: "rgba(0, 0, 0, 0.9)",
                    color: "white",
                    minWidth: "180px",
                    outline: "none",
                    cursor: "pointer",
                    fontSize: "16px",
                    fontWeight: "500",
                    transition: "all 0.3s",
                    appearance: "none",
                    backgroundImage: `url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='white'%3e%3cpath d='M7 10l5 5 5-5z'/%3e%3c/svg%3e")`,
                    backgroundRepeat: "no-repeat",
                    backgroundPosition: "right 15px center",
                    backgroundSize: "20px"
                  }}
                  onMouseEnter={(e) => e.currentTarget.style.borderColor = "#1e88e5"}
                  onMouseLeave={(e) => e.currentTarget.style.borderColor = "rgba(255, 255, 255, 0.3)"}
                >
                  <option value="all"> Todos os pap√©is</option>
                  {uniqueRoles.map((role, index) => (
                    <option key={index} value={role}>
                      {role.charAt(0).toUpperCase() + role.slice(1)}
                    </option>
                  ))}
                </select>
                
                {/* Select de Ordena√ß√£o */}
                <select
                  value={sortBy}
                  onChange={(e) => setSortBy(e.target.value)}
                  style={{
                    padding: "14px 45px 14px 20px",
                    borderRadius: "10px",
                    border: "2px solid rgba(255, 255, 255, 0.3)",
                    background: "rgba(0, 0, 0, 0.9)",
                    color: "white",
                    minWidth: "180px",
                    outline: "none",
                    cursor: "pointer",
                    fontSize: "16px",
                    fontWeight: "500",
                    transition: "all 0.3s",
                    appearance: "none",
                    backgroundImage: `url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='white'%3e%3cpath d='M7 10l5 5 5-5z'/%3e%3c/svg%3e")`,
                    backgroundRepeat: "no-repeat",
                    backgroundPosition: "right 15px center",
                    backgroundSize: "20px"
                  }}
                  onMouseEnter={(e) => e.currentTarget.style.borderColor = "#4ECDC4"}
                  onMouseLeave={(e) => e.currentTarget.style.borderColor = "rgba(255, 255, 255, 0.3)"}
                >
                  <option value="name"> Ordenar por nome</option>
                  <option value="role"> Ordenar por papel</option>
                </select>
              </div>
              
              {/* Info e a√ß√µes */}
              <div style={{ 
                display: "flex", 
                justifyContent: "space-between",
                alignItems: "center",
                color: "white",
                fontSize: "16px",
                flexWrap: "wrap",
                gap: "15px"
              }}>
                <div style={{ display: "flex", alignItems: "center", gap: "15px", flexWrap: "wrap" }}>
                  <span style={{ 
                    color: "#ffffff",
                    background: "rgba(30, 136, 229, 0.2)",
                    padding: "8px 16px",
                    borderRadius: "8px",
                    fontWeight: "bold"
                  }}>
                   {filteredCharacters.length} personagem{filteredCharacters.length !== 1 ? 's' : ''}
                  </span>
                  
                  {searchTerm && (
                    <span style={{ 
                      color: "#90caf9",
                      background: "rgba(144, 202, 249, 0.1)",
                      padding: "8px 16px",
                      borderRadius: "8px"
                    }}>
                       "{searchTerm}"
                    </span>
                  )}
                  
                  {roleFilter !== "all" && (
                    <span style={{ 
                      color: "#81c784",
                      background: "rgba(129, 199, 132, 0.1)",
                      padding: "8px 16px",
                      borderRadius: "8px"
                    }}>
                     {roleFilter}
                    </span>
                  )}
                  
                  <span style={{ 
                    color: "#ffb74d",
                    background: "rgba(255, 183, 77, 0.1)",
                    padding: "8px 16px",
                    borderRadius: "8px"
                  }}>
                   P√°gina {currentPage} de {totalPages}
                  </span>
                </div>
                
                {(searchTerm || roleFilter !== "all") && (
                  <button
                    onClick={() => {
                      setSearchTerm("");
                      setRoleFilter("all");
                      setSortBy("name");
                    }}
                    style={{
                      padding: "10px 20px",
                      background: "linear-gradient(135deg, #ff4444, #cc0000)",
                      color: "white",
                      border: "none",
                      borderRadius: "8px",
                      cursor: "pointer",
                      fontSize: "15px",
                      fontWeight: "bold",
                      transition: "all 0.3s",
                      display: "flex",
                      alignItems: "center",
                      gap: "8px"
                    }}
                    onMouseEnter={(e) => {
                      e.currentTarget.style.transform = "translateY(-2px)";
                      e.currentTarget.style.boxShadow = "0 6px 20px rgba(255, 68, 68, 0.4)";
                    }}
                    onMouseLeave={(e) => {
                      e.currentTarget.style.transform = "translateY(0)";
                      e.currentTarget.style.boxShadow = "none";
                    }}
                  >
                   Limpar filtros
                  </button>
                )}
              </div>
            </div>
          </div>

          {/* Loading */}
          {loading ? (
            <div style={{ 
              textAlign: "center", 
              padding: "80px 20px",
              background: "rgba(0, 0, 0, 0.7)",
              borderRadius: "15px",
              backdropFilter: "blur(10px)",
              border: "2px solid rgba(255, 255, 255, 0.1)"
            }}>
              <div style={{ 
                width: "70px", 
                height: "70px", 
                border: "6px solid rgba(255, 255, 255, 0.1)",
                borderTop: "6px solid #1e88e5",
                borderRadius: "50%",
                margin: "0 auto 25px",
                animation: "spin 1s linear infinite"
              }} />
              <p style={{ 
                color: "white", 
                fontSize: "20px",
                textShadow: "0 2px 4px rgba(0,0,0,0.5)"
              }}>
                Carregando personagens...
              </p>
              <style jsx>{`
                @keyframes spin {
                  0% { transform: rotate(0deg); }
                  100% { transform: rotate(360deg); }
                }
              `}</style>
            </div>
          ) : paginatedCharacters.length === 0 ? (
            <div style={{ 
              textAlign: "center", 
              padding: "80px 20px",
              background: "rgba(0, 0, 0, 0.7)",
              borderRadius: "15px",
              backdropFilter: "blur(10px)",
              border: "2px solid rgba(255, 255, 255, 0.1)"
            }}>
              <div style={{ fontSize: "60px", marginBottom: "20px" }}>üòï</div>
              <p style={{ 
                color: "white", 
                fontSize: "22px", 
                marginBottom: "15px",
                fontWeight: "bold"
              }}>
                Nenhum personagem encontrado
              </p>
              <p style={{ 
                color: "#aaa", 
                fontSize: "16px", 
                marginBottom: "30px",
                maxWidth: "600px",
                margin: "0 auto"
              }}>
                {searchTerm || roleFilter !== "all" 
                  ? `Tente ajustar os filtros de busca.`
                  : "Este anime n√£o possui informa√ß√µes de personagens."}
              </p>
              {(searchTerm || roleFilter !== "all") && (
                <button
                  onClick={() => {
                    setSearchTerm("");
                    setRoleFilter("all");
                    setSortBy("name");
                  }}
                  style={{
                    padding: "14px 28px",
                    background: "linear-gradient(135deg, #1e88e5, #0d47a1)",
                    color: "white",
                    border: "none",
                    borderRadius: "10px",
                    cursor: "pointer",
                    fontSize: "16px",
                    fontWeight: "bold",
                    transition: "all 0.3s",
                    display: "flex",
                    alignItems: "center",
                    gap: "10px",
                    margin: "0 auto"
                  }}
                  onMouseEnter={(e) => e.currentTarget.style.transform = "scale(1.05)"}
                  onMouseLeave={(e) => e.currentTarget.style.transform = "scale(1)"}
                >
                  Limpar filtros
                </button>
              )}
            </div>
          ) : (
            <>
              {/* Grid de Personagens */}
              <div style={{ 
                display: "grid", 
                gridTemplateColumns: "repeat(auto-fill, minmax(300px, 1fr))", 
                gap: 25,
                marginBottom: "40px"
              }}>
                {paginatedCharacters.map((character: any) => (
                  <div 
                    key={character.character.mal_id} 
                    style={{ 
                      background: "rgba(255, 255, 255, 0.08)", 
                      borderRadius: "15px", 
                      padding: "20px",
                      backdropFilter: "blur(10px)",
                      border: "2px solid rgba(255, 255, 255, 0.1)",
                      transition: "all 0.3s ease",
                      cursor: "pointer",
                      height: "100%",
                      display: "flex",
                      flexDirection: "column"
                    }}
                    onMouseEnter={(e) => {
                      e.currentTarget.style.transform = "translateY(-8px)";
                      e.currentTarget.style.borderColor = "#1e88e5";
                      e.currentTarget.style.boxShadow = "0 15px 35px rgba(0, 0, 0, 0.4)";
                    }}
                    onMouseLeave={(e) => {
                      e.currentTarget.style.transform = "translateY(0)";
                      e.currentTarget.style.borderColor = "rgba(255, 255, 255, 0.1)";
                      e.currentTarget.style.boxShadow = "none";
                    }}
                  >
                    <img 
                      src={character.character.images.jpg.image_url} 
                      alt={character.character.name}
                      style={{ 
                        width: "100%", 
                        height: "350px", 
                        objectFit: "cover", 
                        borderRadius: "12px",
                        marginBottom: "18px",
                        border: "3px solid rgba(255, 255, 255, 0.15)"
                      }}
                    />
                    <div style={{ color: "white", flex: 1 }}>
                      <h3 style={{ 
                        margin: "0 0 12px 0", 
                        fontSize: "20px",
                        fontWeight: "bold",
                        lineHeight: "1.4",
                        minHeight: "56px",
                        display: "flex",
                        alignItems: "center"
                      }}>
                        {character.character.name}
                      </h3>
                      <div style={{ 
                        display: "inline-block",
                        background: character.role.toLowerCase() === "main" 
                          ? "linear-gradient(135deg, #1e88e5, #0d47a1)" 
                          : character.role.toLowerCase() === "supporting" 
                          ? "linear-gradient(135deg, #43a047, #2e7d32)" 
                          : "linear-gradient(135deg, #f4511e, #d84315)",
                        color: "white",
                        padding: "8px 16px",
                        borderRadius: "25px",
                        fontSize: "13px",
                        fontWeight: "bold",
                        marginBottom: "15px",
                        boxShadow: "0 3px 10px rgba(0, 0, 0, 0.2)"
                      }}>
                        {character.role}
                      </div>
                      <div style={{ marginTop: "15px" }}>
                        <p style={{ 
                          margin: "8px 0", 
                          fontSize: "16px", 
                          color: "#90caf9",
                          fontWeight: "500"
                        }}>
                          <strong style={{ color: "#ffffff" }}>Voz por:</strong> {character.voice_actors?.[0]?.person?.name || "N√£o informado"}
                        </p>
                        {character.voice_actors?.[0]?.person?.language && (
                          <p style={{ 
                            margin: "4px 0 0 0", 
                            fontSize: "14px", 
                            color: "#aaa",
                            fontStyle: "italic"
                          }}>
                            ({character.voice_actors[0].person.language})
                          </p>
                        )}
                      </div>
                    </div>
                  </div>
                ))}
              </div>

              {/* Pagina√ß√£o */}
              {totalPages > 1 && (
                <div style={{ 
                  display: "flex", 
                  justifyContent: "center", 
                  alignItems: "center",
                  gap: "10px",
                  flexWrap: "wrap",
                  marginBottom: "40px",
                  padding: "25px",
                  background: "rgba(0, 0, 0, 0.7)",
                  borderRadius: "15px",
                  border: "2px solid rgba(255, 255, 255, 0.1)"
                }}>
                  <button
                    onClick={() => handlePageChange(currentPage - 1)}
                    disabled={currentPage === 1}
                    style={{
                      padding: "12px 25px",
                      background: currentPage === 1 
                        ? "rgba(255, 255, 255, 0.1)" 
                        : "linear-gradient(135deg, #1e88e5, #0d47a1)",
                      color: "white",
                      border: "none",
                      borderRadius: "8px",
                      cursor: currentPage === 1 ? "not-allowed" : "pointer",
                      fontWeight: "bold",
                      fontSize: "16px",
                      opacity: currentPage === 1 ? 0.5 : 1,
                      transition: "all 0.3s"
                    }}
                  >
                    ‚Üê Anterior
                  </button>

                  {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                    let pageNum;
                    if (totalPages <= 5) {
                      pageNum = i + 1;
                    } else if (currentPage <= 3) {
                      pageNum = i + 1;
                    } else if (currentPage >= totalPages - 2) {
                      pageNum = totalPages - 4 + i;
                    } else {
                      pageNum = currentPage - 2 + i;
                    }

                    return (
                      <button
                        key={pageNum}
                        onClick={() => handlePageChange(pageNum)}
                        style={{
                          padding: "12px 18px",
                          minWidth: "45px",
                          background: currentPage === pageNum 
                            ? "linear-gradient(135deg, #4ECDC4, #1CE669)" 
                            : "rgba(255, 255, 255, 0.1)",
                          color: "white",
                          border: currentPage === pageNum 
                            ? "2px solid #ffffff" 
                            : "1px solid rgba(255, 255, 255, 0.3)",
                          borderRadius: "8px",
                          cursor: "pointer",
                          fontWeight: "bold",
                          fontSize: "16px",
                          transition: "all 0.3s"
                        }}
                        onMouseEnter={(e) => {
                          if (currentPage !== pageNum) {
                            e.currentTarget.style.background = "rgba(255, 255, 255, 0.2)";
                          }
                        }}
                      >
                        {pageNum}
                      </button>
                    );
                  })}

                  <button
                    onClick={() => handlePageChange(currentPage + 1)}
                    disabled={currentPage === totalPages}
                    style={{
                      padding: "12px 25px",
                      background: currentPage === totalPages 
                        ? "rgba(255, 255, 255, 0.1)" 
                        : "linear-gradient(135deg, #FF6B6B, #FF8E53)",
                      color: "white",
                      border: "none",
                      borderRadius: "8px",
                      cursor: currentPage === totalPages ? "not-allowed" : "pointer",
                      fontWeight: "bold",
                      fontSize: "16px",
                      opacity: currentPage === totalPages ? 0.5 : 1,
                      transition: "all 0.3s"
                    }}
                  >
                    Pr√≥xima ‚Üí
                  </button>

                  <div style={{ 
                    color: "#ffffff", 
                    fontSize: "16px", 
                    marginLeft: "20px",
                    background: "rgba(0, 0, 0, 0.5)",
                    padding: "10px 20px",
                    borderRadius: "8px",
                    border: "1px solid rgba(255, 255, 255, 0.2)"
                  }}>
                    P√°gina <span style={{ color: "#4ECDC4", fontWeight: "bold" }}>{currentPage}</span> de {totalPages}
                  </div>
                </div>
              )}
            </>
          )}
        </div>
      </div>
    </>
  );
};

export default CharactersPage;